<template>
    <section>
        <!--工具条-->
        <toolbar
            :buttonList="buttonList"
            placeholder="分组名称、分组简称过滤"
            @callFunction="callFunction"
        ></toolbar>
        <!--列表-->
        <el-table
            :data="Groups"
            :row-class-name="$tableRowClassName"
            v-loading="listLoading"
            style="width: 100%"
        >
            <!-- <el-table-column type="selection" width="50"> </el-table-column> -->
            <el-table-column type="index" width="80">
                <template slot-scope="scope">
                    <div
                        v-text="(page - 1) * pageSize + 1 + scope.$index"
                    ></div>
                </template>
            </el-table-column>
            <el-table-column prop="groupname" label="分组名称" min-width="350">
            </el-table-column>
            <el-table-column
                prop="groupshortname"
                label="分组简称"
                min-width="200"
            >
            </el-table-column>
            <el-table-column
                prop="CityName"
                min-width="150"
                label="城市"
            ></el-table-column>
            <el-table-column
                prop="DistrictName"
                min-width="150"
                label="区"
            ></el-table-column>
            <el-table-column prop="hastsp" label="TSP模块" min-width="150"
                ><template slot-scope="scope">
                    <el-tag
                        effect="dark"
                        :type="scope.row.hastsp == 1 ? 'success' : 'info'"
                        >TSP</el-tag
                    >
                </template>
            </el-table-column>
            <el-table-column
                label="扬尘超标线管理"
                min-width="150"
                v-if="canEditLine"
            >
                <template slot-scope="scope">
                    <el-button
                        v-if="canEditLine"
                        plain
                        size="small"
                        @click="handleEditLine(scope.$index, scope.row)"
                        >扬尘超标线</el-button
                    >
                </template>
            </el-table-column>

            <el-table-column prop="status" label="状态" min-width="180">
                <template slot-scope="scope">
                    <el-switch
                        v-model="scope.row.status"
                        :active-value="0"
                        active-text="正常"
                        :inactive-value="1"
                        inactive-text="冻结"
                        @change="changeSwitch(scope.$index, scope.row)"
                    ></el-switch>
                </template>
            </el-table-column>
            <el-table-column
                label="操作"
                width="150"
                v-if="canEdit || canDelete"
            >
                <template slot-scope="scope">
                    <el-button
                        v-if="canEdit"
                        plain
                        type="warning"
                        size="small"
                        @click="handleEdit(scope.$index, scope.row)"
                        >编辑</el-button
                    >
                    <!-- <el-button
                        v-if="canDelete"
                        plain
                        type="danger"
                        size="small"
                        @click="handleDel(scope.$index, scope.row)"
                        >删除</el-button
                    > -->
                </template>
            </el-table-column>
        </el-table>

        <!--工具条-->
        <el-col :span="24" class="toolbar">
            <el-pagination
                layout="total, prev, pager, next, jumper"
                @current-change="handleCurrentChange"
                :page-size="pageSize"
                :current-page="page"
                :total="total"
                style="float: right"
            >
            </el-pagination>
        </el-col>

        <!--编辑界面-->
        <el-dialog
            title="编辑"
            :visible.sync="editFormVisible"
            v-model="editFormVisible"
            :close-on-click-modal="false"
        >
            <el-form
                :model="editForm"
                label-width="100px"
                :rules="editFormRules"
                ref="editForm"
            >
                <el-row>
                    <el-col :span="12">
                        <el-form-item label="分组名称" prop="groupname">
                            <el-input
                                maxlength="40"
                                v-model="editForm.groupname"
                                auto-complete="off"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="分组简称" prop="groupshortname">
                            <el-input
                                maxlength="6"
                                v-model="editForm.groupshortname"
                                auto-complete="off"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-col :span="24">
                            <el-form-item label="所属机构" prop="belongto">
                                <el-input
                                    maxlength="50"
                                    v-model="editForm.belongto"
                                    auto-complete="off"
                                    placeholder="示范片区必填"
                                ></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="24">
                            <el-form-item label="城市" prop="city">
                                <el-select
                                    v-model="editForm.city"
                                    filterable
                                    placeholder="请选择"
                                    @change="changeCity"
                                >
                                    <el-option
                                        v-for="item in citys"
                                        :key="item.RegionId"
                                        :label="item.RegionName"
                                        :value="item.RegionId"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="24">
                            <el-form-item label="区" prop="district">
                                <el-select
                                    v-model="editForm.district"
                                    filterable
                                    clearable
                                    placeholder="请选择"
                                >
                                    <el-option
                                        v-for="item in districts"
                                        :key="item.RegionId"
                                        :label="item.RegionName"
                                        :value="item.RegionId"
                                    >
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>

                        <el-col :span="24">
                            <el-row>
                                <el-col :span="16">
                                    <el-form-item
                                        label="经纬度"
                                        prop="longitude"
                                        ><el-input
                                            v-model="editForm.longitude"
                                            auto-complete="off"
                                            maxlength="30"
                                            placeholder="示范片区必填"
                                        ></el-input></el-form-item
                                ></el-col>
                                <el-col :span="8">
                                    <el-form-item
                                        prop="latitude"
                                        label-width="0"
                                    >
                                        <el-input
                                            v-model="editForm.latitude"
                                            auto-complete="off"
                                            maxlength="30"
                                        ></el-input>
                                    </el-form-item>
                                </el-col>
                            </el-row>
                        </el-col>
                        <el-col :span="24">
                            <el-form-item label="TSP模块" prop="hastsp">
                                <el-switch
                                    v-model="editForm.hastsp"
                                    active-text="是"
                                    inactive-text="否"
                                    :active-value="1"
                                    :inactive-value="0"
                                >
                                </el-switch>
                            </el-form-item>
                        </el-col>
                    </el-col>
                    <el-col :span="12">
                        <mark-map
                            ref="myMap"
                            v-if="editFormVisible"
                            :markposition="{
                                lng: editForm.longitude,
                                lat: editForm.latitude,
                            }"
                            @callMap="callEditMap"
                        ></mark-map>
                    </el-col>
                </el-row>

                <el-row>
                    <!-- <el-col :span="12">
                        <el-form-item label="网站配色" prop="skin">
                            <el-input
                                v-model="editForm.skin"
                                auto-complete="off"
                            ></el-input>
                        </el-form-item>
                    </el-col> -->
                </el-row>

                <el-form-item label="网站名称" prop="logo">
                    <el-input
                        v-model="editForm.logo"
                        auto-complete="off"
                        maxlength="100"
                    ></el-input>
                </el-form-item>
                <el-form-item label="简介" prop="summary">
                    <el-input
                        v-model="editForm.summary"
                        type="textarea"
                        auto-complete="off"
                        maxlength="2000"
                        rows="7"
                    ></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editFormVisible = false"
                    >取消</el-button
                >
                <el-button
                    type="primary"
                    @click.native="editSubmit"
                    :loading="editLoading"
                    >提交</el-button
                >
            </div>
        </el-dialog>

        <!--扬尘超标线界面-->
        <el-dialog
            title="扬尘告警线设置"
            :visible.sync="editLineFormVisible"
            v-model="editLineFormVisible"
            :close-on-click-modal="false"
            width="30%"
        >
            <el-form
                :model="editLineForm"
                :rules="editLineFormRules"
                ref="editLineForm"
            >
                <el-row>
                    <el-col :span="24">
                        <el-form-item label="PM2.5超标线" prop="pm2_5warnline">
                            <el-input
                                maxlength="50"
                                v-model="editLineForm.pm2_5warnline"
                                auto-complete="off"
                                type="number"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="24">
                        <el-form-item label="PM10超标线" prop="pm10warnline">
                            <el-input
                                maxlength="50"
                                v-model="editLineForm.pm10warnline"
                                auto-complete="off"
                                type="number"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="24">
                        <el-form-item label="TSP超标线" prop="tspwarnline">
                            <el-input
                                maxlength="50"
                                v-model="editLineForm.tspwarnline"
                                auto-complete="off"
                                type="number"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editLineFormVisible = false"
                    >取消</el-button
                >
                <el-button
                    type="primary"
                    @click.native="editLineSubmit"
                    :loading="editLineLoading"
                    >提交</el-button
                >
            </div>
        </el-dialog>
    </section>
</template>
<script>
import util from "../../../util/date";
import {
    getGroupListPage,
    removeGroup,
    editGroup,
    editGroupLine,
    addGroup,
    updateStatus,
    City,
    getGroupSetting,
} from "@/api/api";
import { getButtonList } from "../../promissionRouter";
import Toolbar from "../../components/Toolbar";
import MarkMap from "../../components/MarkMap";
export default {
    components: { Toolbar, MarkMap },
    data() {
        return {
            buttonList: [],
            filters: {
                name: "",
            },
            Groups: [],
            total: 0,
            page: 1,
            pageSize: 10,
            listLoading: false,
            editFormVisible: false, //编辑界面是否显示
            editLineFormVisible: false,
            editLoading: false,
            editLineLoading: false,
            editFormRules: {
                groupname: [
                    {
                        required: true,
                        message: "请输入分组名称",
                        trigger: "blur",
                    },
                ],
                groupshortname: [
                    {
                        required: true,
                        message: "请输入分组简称",
                        trigger: "blur",
                    },
                ],
                city: [
                    {
                        required: true,
                        message: "请选择城市",
                        trigger: "blur",
                    },
                ],
            },
            editLineFormRules: {
                pm10warnline: [
                    {
                        required: true,
                        message: "请输入此项",
                        trigger: "blur",
                    },
                ],
                pm2_5warnline: [
                    {
                        required: true,
                        message: "请输入此项",
                        trigger: "blur",
                    },
                ],
                tspwarnline: [
                    {
                        required: true,
                        message: "请输入此项",
                        trigger: "blur",
                    },
                ],
            },
            editForm: {
                GroupId: "",
            },
            editLineForm: {
                GroupId: "",
                pm10warnline: "",
                pm2_5warnline: "",
                tspwarnline: "",
            },
            canEdit: false,
            canDelete: false,
            citys: [],
            districts: [],
            isEdit: false,
            canEditLine: false,
        };
    },
    methods: {
        callFunction(item) {
            this.filters = {
                name: item.search,
            };
            this[item.Func].apply(this, item);
        },
        handleCurrentChange(val) {
            this.getGroups(val);
        },
        getGroups(index) {
            if (index) {
                this.page = index;
            } else {
                this.page = 1;
            }
            let para = {
                page: this.page,
                size: this.pageSize,
                keyword: this.filters.name,
            };
            this.listLoading = true;

            getGroupListPage(para).then((res) => {
                this.total = res.data.dataCount;
                this.Groups = res.data.data;
                this.listLoading = false;
            });
        },
        //删除
        handleDel(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要删除的一行数据！",
                    type: "error",
                });

                return;
            }
            this.$confirm("确认删除该记录吗?", "提示", {
                type: "warning",
            })
                .then(() => {
                    this.listLoading = true;
                    let para = { GroupId: row.GroupId };
                    removeGroup(para).then((res) => {
                        if (util.isEmt.format(res)) {
                            this.listLoading = false;
                            return;
                        }
                        this.listLoading = false;
                        if (res.success) {
                            this.$message({
                                message: "删除成功",
                                type: "success",
                            });
                            this.clearGroupSites();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.getGroups();
                    });
                })
                .catch(() => {});
        },
        //显示编辑界面
        async handleEdit(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要编辑的一行数据！",
                    type: "error",
                });

                return;
            }

            this.isEdit = true;

            if (row.city > 0) {
                await this.getAreas(row.city);
                this.editForm = Object.assign({}, row);
            } else {
                this.editForm = Object.assign({}, row);
            }

            if (this.editForm["district"] == 0) {
                this.editForm["district"] = "";
            }
            this.editFormVisible = true;
        },
        //显示扬尘超标线界面
        async handleEditLine(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要编辑的一行数据！",
                    type: "error",
                });

                return;
            }

            this.editLineForm = {};

            let para = { groupid: row.GROUPID };
            var result = await getGroupSetting(para);

            this.isEdit = true;
            this.editLineFormVisible = true;

            this.$set(this.editLineForm, "GroupId", row.GROUPID);
            result.data.forEach((e) => {
                this.$set(this.editLineForm, e.key, e.value);
            });
        },
        //显示新增界面
        handleAdd() {
            this.isEdit = false;
            this.editForm = {};
            this.editFormVisible = true;
        },
        callEditMap(item) {
            // this.editForm.longitude = item.point.lng;
            // this.editForm.latitude = item.point.lat;

            this.$set(this.editForm, "longitude", item.point.lng);
            this.$set(this.editForm, "latitude", item.point.lat);
        },
        //编辑
        editSubmit: function () {
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    this.editLoading = true;
                    let para = Object.assign({}, this.editForm);
                    if (!para["district"]) {
                        para["district"] = 0;
                    }
                    if (this.isEdit) {
                        editGroup(para).then((res) => {
                            if (util.isEmt.format(res)) {
                                this.editLoading = false;
                                return;
                            }
                            if (res.success) {
                                this.$message({
                                    message: "保存成功",
                                    type: "success",
                                });
                                this.$refs["editForm"].resetFields();
                                this.editFormVisible = false;
                                this.clearGroupSites();
                            } else {
                                let msg = "保存失败";
                                if (res.msg) {
                                    msg = res.msg;
                                }
                                this.$message({
                                    message: msg,
                                    type: "error",
                                });
                            }

                            this.editLoading = false;
                        });
                    } else {
                        para.status = 0; // 0:正常  1:冻结
                        addGroup(para).then((res) => {
                            if (util.isEmt.format(res)) {
                                this.editLoading = false;
                                return;
                            }

                            if (res.success) {
                                this.editLoading = false;
                                this.$message({
                                    message: "新增成功",
                                    type: "success",
                                });
                                this.$refs["editForm"].resetFields();
                                this.editFormVisible = false;
                                this.clearGroupSites();
                            } else {
                                let msg = "保存失败";
                                if (res.msg) {
                                    msg = res.msg;
                                }
                                this.$message({
                                    message: msg,
                                    type: "error",
                                });
                            }
                        });
                    }
                }
            });
        },
        //设置扬尘超标线
        editLineSubmit: function () {
            this.$refs.editLineForm.validate((valid) => {
                if (valid) {
                    this.editLineLoading = true;
                    let para = Object.assign({}, this.editLineForm);

                    editGroupLine(para).then((res) => {
                        if (util.isEmt.format(res)) {
                            this.editLineLoading = false;
                            return;
                        }
                        if (res.success) {
                            this.$message({
                                message: "保存成功",
                                type: "success",
                            });
                            this.$refs["editLineForm"].resetFields();
                            this.editLineFormVisible = false;
                            this.getGroups();
                        } else {
                            let msg = "保存失败";
                            if (res.msg) {
                                msg = res.msg;
                            }
                            this.$message({
                                message: msg,
                                type: "error",
                            });
                        }

                        this.editLineLoading = false;
                    });
                }
            });
        },
        //新增
        addSubmit: function () {
            let _this = this;
            alert(JSON.stringify(valid));
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                }
            });
        },
        changeSwitch(index, row) {
            let para = row.GROUPID;
            updateStatus(para).then((res) => {
                if (res.success) {
                    this.$message({
                        message: "更新成功",
                        type: "success",
                    });
                    this.clearGroupSites();
                } else {
                    let r = row;
                    r.status = row.status == 0 ? 1 : 0;
                    this.Groups[index] = r;
                    this.$message({
                        message: "更新失败",
                        type: "error",
                    });
                }
            });
        },
        getCitys(pid) {
            let para = { pid: pid };
            City(para).then((res) => {
                if (res.success) {
                    this.citys = res.data;
                }
            });
        },
        async getAreas(pid) {
            let para = { pid: pid };
            City(para).then((res) => {
                if (res.success) {
                    this.districts = res.data;
                }
            });
        },

        changeCity(val) {
            this.districts = [];
            if (val != "") {
                this.getAreas(val);
            }
            this.$set(this.editForm, "district", "");
        },
    },
    mounted() {
        this.getGroups();
        this.getCitys(0);
        let routers = window.sessionStorage.router
            ? JSON.parse(window.sessionStorage.router)
            : [];
        let buttons = getButtonList(this.$route.path, routers);
        buttons.forEach((element) => {
            //&& element.IsButton && !element.IsHide
            if (element.Func == "handleEdit") {
                this.canEdit = true;
            } else if (element.Func == "handleDel") {
                this.canDelete = true;
            } else if (element.Func == "handleEditLine") {
                this.canEditLine = true;
            } else {
                this.buttonList.push(element);
            }
        });
    },
};
</script>
<style scoped>
.bm-view {
    padding-left: 10px;
    width: 100%;
    height: 292px;
    margin-bottom: 10px;
}
</style>


